.header{
    text-align: center;
	font-family: 'Montserrat';
	width: 900px;
	position: absolute;
    left: 50%;
	top: 55%;
	transform: translate(-50%,-55%);
}
.header span{
    color: #FF1414;
}
.header .details{
    font-size: 23px;
    color: #c5c5c5;
}
.header h2{
	font-family:'Lato';
	font-size: 2.7rem;
	margin:0px;
	letter-spacing: 2px;
	color:#ffffff;
}
.header h1{
	font-family:'Lato';
	font-size: 5rem;
	margin:0px;
	letter-spacing: 2px;
	color:#ffffff;
}

.header-heading > h2{
    color:#ffffff;
}

.header-btns{
    display: flex;
    margin-top: 40px;
    margin-left: 40%;
}
.header-btn{
	width:160px;
	height: 50px;
	display: flex;
	justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    background-color: #FF1414;
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
    border-radius: 5px;
	color:#ffffff;
}
.header-btn:hover{
	background-color: transparent;
    transition: all ease 0.5s;
    color: #ffffff;
    border: 2px solid #FF1414;
}

@media(max-width:1190px){
	.header{
		left: 50%;
		top: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
    }
    .header h2{
        font-size: 1.5rem;
    }
    .header h1{
        font-size: 3rem;
    }
    .header .details{
        font-size: 1rem;
    }
}

@media(max-width:970px){
	.header{
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
    }
   
	
    .header-btn{
        margin-left: 20%;
        margin-top: 20px;
    }    
}

@media(max-width:600px){
	.header{
		width:60%;
    }
   
}